h1 {
  font-size: 1.8rem;
}
h2 {
  font-size: 1.4rem;
  margin: 1rem;
  letter-spacing: 0.05rem;
}
h3 {
  font-size: 1.1rem;
  margin: 1rem;
  letter-spacing: 0.02rem;
}
h5 {
  font-size: 0.9rem;
}
h6 {
  font-size: 0.6rem;
  opacity: 50%;
}
.main {
  width: 100vw;
  @include flex-box;
}
.about {
  @include flex-column;

  width: 50vw;
}
.skills {
  @include flex-column;

  p {
    @include flex-row;
    font-size: 2rem;
    margin-bottom: 0.5rem;
    // >>>>> TOOLTIP <<<<<
    .toolTip {
      position: relative;
      display: inline-block;
      margin: 1rem;
    }
    .toolTip .tTtext {
      visibility: hidden;
      font-size: 1.2rem;
      height: 2rem;
      background-color: white;
      color: lightcoral;
      text-align: center;
      position: absolute;
      z-index: 1;
    }
    .toolTip:hover .tTtext {
      padding: 0.7rem;
      visibility: visible;
    }
  }
}

.imprint {
  @include flex-column;
  .row {
    @include flex-row;
  }
  span {
    font-size: 1.6rem;
    animation: cookie 4s infinite;
  }
}
